<template>
  <div class="auth-card card p-4">
    <ul class="nav nav-tabs" id="authTab" role="tablist">
      <li class="nav-item" role="presentation">
        <button
          class="nav-link active"
          id="register-tab"
          data-bs-toggle="tab"
          data-bs-target="#register"
          type="button"
          role="tab"
          aria-controls="register"
          aria-selected="false"
        >
          Регистрация
        </button>
      </li>
    </ul>
    <div class="tab-content" id="authContent">
      <div
        class="tab-pane fade show active"
        id="register"
        role="tabpanel"
        aria-labelledby="register-tab"
      >
        <div class="mt-3">
          <form @submit.prevent="onRegisterSubmit">
            <div class="mb-3">
              <label for="registerEmail" class="form-label">Email</label>
              <input
                type="email"
                :class="['form-control', { 'is-invalid': rEmailError }]"
                id="registerEmail"
                aria-describedby="emailHelp"
                placeholder="name@example.com"
                v-model="registerEmail"
              />
              <div class="form-text text-danger" v-if="rEmailError">
                {{ rEmailError }}
              </div>
            </div>
            <div class="mb-3">
              <label for="registerFio" class="form-label">ФИО</label>
              <input
                type="text"
                :class="['form-control', { 'is-invalid': rNameError }]"
                id="registerFio"
                aria-describedby="emailHelp"
                placeholder="ФИО"
                v-model="registerName"
              />
              <div class="form-text text-danger" v-if="rNameError">
                {{ rNameError }}
              </div>
            </div>
            <div class="mb-3">
              <label for="registerPassword" class="form-label">Пароль</label>
              <input
                type="password"
                :class="['form-control', { 'is-invalid': rPasswordError }]"
                id="registerPassword"
                placeholder="Пароль"
                v-model="registerPassword"
              />
              <div class="form-text text-danger" v-if="rPasswordError">
                {{ rPasswordError }}
              </div>
            </div>
            <button type="submit" class="btn btn-primary">
              Регистрация
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useRegisterForm } from "../../use/register-form";

export default {
  setup() {
    return { ...useRegisterForm() };
  },
};
</script>

<style></style>
